<template>
    <table className='table table-striped table-hover'>
          <thead>
              <tr>
                  <th>Fecha</th>
                  <th>Descripcion</th>
                  <th>Monto</th>
                  <th>Editar</th>
                  <th>Eliminar</th>
              </tr>     
          </thead>
          <tbody>
   
              <tr  v-for="egr in getEgresosAllFerney" :key="egr._id">
                  <td>{{egr.fecha.slice(0,10)}}</td>
                  <td>{{egr.descripcion}}</td>
                  <td>{{egr.monto}}</td>
  
                  <td>
                      <button
                          className='btn btn-secondary'
                          @click="setEgresoFerney(egr)"
                          :disabled="getIsLoadingFerney"
                          
                      >
                          Editar
                      </button>
                  </td>
                  <td>
                      <button
                          className='btn btn-danger'
                          @click="deleteEgresoFerney(egr._id)"
                          :disabled="getIsLoadingFerney"
                          
                      >
                          Eliminar
                      </button>
                  </td>
  
              </tr>
  
          </tbody>
      
      </table>
      
  </template>
  
  <script>
  import { mapActions, mapGetters, mapMutations } from 'vuex';
  
  export default {
     
      computed:{
          ...mapGetters('ferneyEgreso',['getEgresosAllFerney','getIsLoadingFerney']),
          ...mapGetters('auth',['getPerfil'])
      },
      methods:{
          ...mapActions('ferneyEgreso',['getEgresosFerney','deleteEgresoFerney']),
          ...mapMutations('ferneyEgreso',['setEgresoFerney'])
      },
      created(){
          this.getEgresosFerney()
      }
  }
  </script>
  
  <style>
  
  </style>